import React from "react";
import ReactFullpage from "@fullpage/react-fullpage";
import { MainContext } from "./Sections/MainContext";
import { Section } from "./Section";
import { Settings, Settings2, Settings3, Settings4, Settings5, Settings6, Settings7, Settings8, Settings9, Settings10, Settings11 } from "./Sections/Settings";

export const anchors = ["se01", "se02", "se03", "se04", "se05", "se06", "se07", "se08", "se09", "se10", "se11"];

export default function SectionsEntry() {
    return (
        <ReactFullpage
            anchors={anchors}
            navigation
            navigationTooltips={anchors}
            licenseKey='YOUR_KEY_HERE'
            render={({ state, fullpageApi }) => {
                return (
                    <div>
                        <Section>
                            <MainContext number="01" title=" " content={Settings()} />
                        </Section>

                        <Section>
                            <MainContext number="02" title=" " content={Settings2()} />
                        </Section>

                        <Section>
                            <MainContext number="03" title=" " content={Settings3()} />
                        </Section>

                        <Section>
                            <MainContext number="04" title=" " content={Settings4()} />
                        </Section>

                        <Section>
                            <MainContext number="05" title=" " content={Settings5()} />
                        </Section>

                        <Section>
                            <MainContext number="06" title=" " content={Settings6()} />
                        </Section>

                        <Section>
                            <MainContext number="07" title=" " content={Settings7()} />
                        </Section>

                        <Section>
                            <MainContext number="08" title="" content={Settings8()} />
                        </Section>

                        <Section>
                            <MainContext number="09" title=" " content={Settings9()} />
                        </Section>

                        <Section>
                            <MainContext number="10" title="  " content={Settings10()} />
                        </Section>

                        <Section>
                            <MainContext number="11" title="  " content={Settings11()} />
                        </Section>
                    </div>
                );
            }} />
    );
}