import React from "react";
import { stateViewEnum, SetChangeViewState } from "../stateViewEnum";

export function ContentsButton() {
    return (
        <div className="btn-wrapper">
            <button className="btn2" onClick={()=>SetChangeViewState(stateViewEnum.system)}></button>
            <button className="btn" onClick={()=>SetChangeViewState(stateViewEnum.stage)}></button>
            <button className="btn2" onClick={()=>SetChangeViewState(stateViewEnum.gimmick)}></button>
            <button className="btn" style={{ visibility: "hidden" }}></button>
            <br />
            <button className="btn" onClick={()=>SetChangeViewState(stateViewEnum.gui)}>GUI</button>
            <button className="btn2" onClick={()=>SetChangeViewState(stateViewEnum.art)}></button>
            <button className="btn" onClick={()=>SetChangeViewState(stateViewEnum.story)}></button>
            <button className="btn2" onClick={()=>SetChangeViewState(stateViewEnum.settings)}> </button>
        </div>
    );
}