import React from "react";
import ReactFullpage from "@fullpage/react-fullpage";
import { MainContext } from "./Sections/MainContext";
import { Section } from "./Section";
import { System, System2, System3, System4, System5, System6, System7, System8, System9, System10 } from "./Sections/System";
import { System11, System12, System13, System14, System15, System16, System17 } from "./Sections/System";

export const anchors = [
    "sy01", "sy02", "sy03", "sy04", "sy05", "sy06", "sy07", "sy08", "sy09", "sy10",
    "sy11", "sy12", "sy13", "sy14", "sy15", "sy16", "sy17"
];

export default function SectionsStory() {
    return (
        <ReactFullpage
            anchors={anchors}
            navigation
            navigationTooltips={anchors}
            licenseKey='YOUR_KEY_HERE'
            render={({ state, fullpageApi }) => {
                return (
                    <div>
                        <Section>
                            <MainContext number="01" title="" content={System()} />
                        </Section>

                        <Section>
                            <MainContext number="02" title=" " content={System2()} />
                        </Section>

                        <Section>
                            <MainContext number="04" title=" " content={System3()} />
                        </Section>

                        <Section>
                            <MainContext number="05" title="  - " content={System4()} />
                        </Section>

                        <Section>
                            <MainContext number="06" title="  -  " content={System5()} />
                        </Section>

                        <Section>
                            <MainContext number="07" title="  - " content={System6()} />
                        </Section>

                        <Section>
                            <MainContext number="08" title="" content={System7()} />
                        </Section>

                        <Section>
                            <MainContext number="09" title=" - " content={System8()} />
                        </Section>

                        <Section>
                            <MainContext number="10" title=" - " content={System9()} />
                        </Section>

                        <Section>
                            <MainContext number="11" title=" - " content={System10()} />
                        </Section>

                        <Section>
                            <MainContext number="12" title=" " content={System11()} />
                        </Section>

                        <Section>
                            <MainContext number="13" title=" " content={System12()} />
                        </Section>

                        <Section>
                            <MainContext number="14" title=" " content={System13()} />
                        </Section>

                        <Section>
                            <MainContext number="15" title="" content={System14()} />
                        </Section>

                        <Section>
                            <MainContext number="16" title="" content={System15()} />
                        </Section>

                        <Section>
                            <MainContext number="17" title="" content={System16()} />
                        </Section>

                        <Section>
                            <MainContext number="18" title="" content={System17()} />
                        </Section>
                    </div>
                );
            }} />
    );
}